//src/components/Room.js
import React, { useState } from "react";
import styled from "styled-components";
import Modal from "./Modal"; // Modal  import

const RoomWrapper = styled.div`
  margin-top: 0;
  margin-right: 70px;
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease;
  border-radius: 10px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  &:hover {
    transform: scale(1.05);
  }
`;

const RoomImage = styled.img`
  width: 130px;
  margin: 4px;
`;

const RoomFloor = styled.p`
  color: #000;
  font-size: 14px;
  font-weight: bold;
  margin: 0px;
`;

const RoomType = styled.p`
  margin: 0px;
  color: #000;
  font-size: 12px;
`;

function Room({roomData}) { // roomData    
  const [isModalOpen, setIsModalOpen] = useState(false); //   
  const [reservationInfo, setReservationInfo] = useState({}); //  

  const date = roomData.date;
  const time = roomData.time;

  // Room    
  const handleRoomClick = () => {
    setIsModalOpen(true);
  };

  //  
  const closeModal = () => {
    setIsModalOpen(false);
  };

  //     
  const handleReservation = () => {
    setIsModalOpen(false); //  
  };

  return (
    <>
      <RoomWrapper onClick={handleRoomClick}>
        <RoomImage src={roomData.image} alt="" /> {/*    */}
        <RoomFloor>{roomData.roomFloor}</RoomFloor> {/*    */}
        <RoomType>{roomData.roomName}</RoomType> {/*    */}
      </RoomWrapper>
      <Modal
        isOpen={isModalOpen}
        onClose={closeModal}
        onReservation={handleReservation} //     
        date={roomData.date}
        time={roomData.time}
        roomData = {roomData}
      />
    </>
  );
}

export default Room;
