import React, {useEffect, useState} from 'react';
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import Main from './pages/Main';
import FacilityReservation from './pages/FacilityReservation';
import Signup from "./pages/Signup";
import Login from "./pages/Login";
import PopularBooks from "./pages/PopularBooks";
import MyPage from "./pages/MyPage";
import BookDetail from "./pages/BookDetail";
import SearchResult from "./pages/SearchResult"; // SearchResult 

function App() {
    const [closingWindow, setClosingWindow] = useState(false);

    //      
    function clearTokens() {
        if (closingWindow) {
            localStorage.removeItem("userInfo");
            localStorage.removeItem("token");
        }
    }

    useEffect(() => {
        //        
        window.addEventListener("beforeunload", () => {
            setClosingWindow(true); //    closingWindow true 
            clearTokens(); //    
        });

        //   window     
        window.addEventListener("mouseenter", () => {
            setClosingWindow(false);
        });

        //  ALT+TAB        
        window.addEventListener("keydown", (e) => {
            if (e.altKey && e.key === "Tab") {
                setClosingWindow(false);
            }
        });

        //  F5, CTRL+F5, CTRL+R      
        window.addEventListener("keydown", (e) => {
            if ((e.key === "F5" || (e.ctrlKey && e.key === "r")) && !e.repeat) {
                setClosingWindow(false);
            }
        });

        // a      
        document.addEventListener("click", (e) => {
            if (e.target.tagName === "A") {
                setClosingWindow(false);
            }
        });

        //         
        document.addEventListener("click", (e) => {
            if (e.target.tagName === "BUTTON") {
                setClosingWindow(false);
            }
        });

        // form submit    
        document.addEventListener("submit", () => {
            setClosingWindow(false);
        });

        //      
        return () => {
            window.removeEventListener("beforeunload", () => {
            });
            window.removeEventListener("mouseenter", () => {
            });
            window.removeEventListener("keydown", () => {
            });
            window.removeEventListener("keydown", () => {
            });
            document.removeEventListener("click", () => {
            });
            document.removeEventListener("click", () => {
            });
            document.removeEventListener("submit", () => {
            });
        };
    }, []);
    return (
        <Router>
            <Routes>
                <Route path="/" element={<Main/>}/>
                <Route path="/facility-reservation" element={<FacilityReservation/>}/>
                <Route path="/popularbooks" element={<PopularBooks/>}/>
                <Route path="/signup" element={<Signup/>}/>
                <Route path="/login" element={<Login/>}/>
                <Route path="/mypage" element={<MyPage/>}/>
                <Route path="/book-detail/:bookId" element={<BookDetail/>}/>
                <Route path="/results/:keyword" element={<SearchResult/>}/> {/* SearchResult    */}
            </Routes>
        </Router>
    );
}

export default App;
