import React, { useState } from 'react';
import styled from 'styled-components';
import Header from '../components/Header';
import Button from '../common/Button';
import { useNavigate } from "react-router-dom";

const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 30px;
  margin-top: 180px;
`;

const Title = styled.p`
  font-size: 30px;
  font-weight: bold;
  margin-top: 20px;
`;

const FormTable = styled.table`
  width: 700px;
  margin-top: 20px;
  border-collapse: collapse;
  margin-bottom: 40px;
`;

const ColGroup = styled.colgroup`
  col:nth-child(1) {
    width: 20%;
  }

  col:nth-child(2) {
    width: 80%;
  }
`;

const TBody = styled.tbody`
  /*       . */

  tr:not(:last-of-type) {
    border-bottom: 1px solid #eaeaea;
  }
`;

const TableRow = styled.tr`
  td {
    padding: 30px;
  }
`;

const InputField = styled.input`
  width: 100%;
  height: 35px;
  border: 1px solid #c4c4c4;
  padding: 2.5px;
`;

const InputInform = styled.p`
  font-size: 14px;
  margin: 0;
`;
const SelectGrade = styled.select`
  width: 200px;
  height: 35px;
  border: 1px solid #c4c4c4;
`;

const SignupForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;
function Signup() {
    const [formData, setFormData] = useState({
        studentID: '',
        userName: '',
        major: '',
        grade: '1',
        email: '',
        phoneNumber: '',
        userID: '',
        password: '',
        confirmPassword: '', //   
    });

    //    
    const [passwordMatch, setPasswordMatch] = useState('');

    const navigate = useNavigate();
    const handlePasswordChange = (e) => {
        const password = e.target.value;
        setFormData({
            ...formData,
            password,
        });
        checkPasswordMatch(password, formData.confirmPassword);
    };

    const handleConfirmPasswordChange = (e) => {
        const confirmPassword = e.target.value;
        setFormData({
            ...formData,
            confirmPassword,
        });
        checkPasswordMatch(formData.password, confirmPassword);
    };

    const checkPasswordMatch = (password, confirmPassword) => {
        if (password === confirmPassword) {
            setPasswordMatch(' ');
        } else {
            setPasswordMatch('   .');
        }
    };
    const handleSubmit = async (e) => {
        e.preventDefault();

        //    
        if (formData.password !== formData.confirmPassword) {
            setPasswordMatch('   .');
            return; //       
        }

        // :      
        if (
            formData.studentID &&
            formData.userName &&
            formData.major &&
            formData.grade &&
            formData.email &&
            formData.phoneNumber &&
            formData.userID &&
            formData.password &&
            formData.confirmPassword
        ) {
            //      ,   

            // API  URL
            const apiUrl = 'http://localhost:8080/api/signup';

            //    
            const requestBody = {
                userStuId: formData.studentID,
                userName: formData.userName,
                userMajor: formData.major,
                userGrade: formData.grade,
                userEmail: formData.email,
                userPnum: formData.phoneNumber,
                userId: formData.userID, //  
                userPw: formData.password,
            };

            try {
                const response = await fetch(apiUrl, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify(requestBody),
                });

                if (response.ok) {
                    const responseData = await response.json();
                    console.log(' :', responseData);
                    alert('     .');
                    //      
                    navigate('/login');

                } else {
                    //  
                    console.error(' :', response.statusText);
                    alert(' .');
                }
            } catch (error) {
                console.error(' :', error);
                alert('   .');
            }
        } else {
            alert('    .');
        }
    };
    const handleInputChange = (e) => {
        const {name, value} = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });

        setPasswordMatch(''); //       
    };

    return (
        <Wrapper>
            <Header/>
            <Title></Title>
            <SignupForm onSubmit={handleSubmit}>
                <FormTable>
                    <ColGroup>
                        <col/>
                        <col/>
                    </ColGroup>
                    <TBody>
                        <TableRow>
                            <td>
                                <InputInform></InputInform>
                            </td>
                            <td>
                                <InputField
                                    type="text"
                                    name="studentID"
                                    value={formData.studentID}
                                    onChange={handleInputChange}
                                />
                            </td>
                        </TableRow>
                        <TableRow>
                            <td>
                                <InputInform></InputInform>
                            </td>
                            <td>
                                <InputField
                                    type="text"
                                    name="userName"
                                    value={formData.userName}
                                    onChange={handleInputChange}
                                />
                            </td>
                        </TableRow>
                        <TableRow>
                            <td>
                                <InputInform></InputInform>
                            </td>
                            <td>
                                <InputField
                                    type="text"
                                    name="major"
                                    value={formData.major}
                                    onChange={handleInputChange}
                                />
                            </td>
                        </TableRow>
                        <TableRow>
                            <td>
                                <InputInform></InputInform>
                            </td>
                            <td>
                                <SelectGrade
                                    name="grade"
                                    value={formData.grade}
                                    onChange={handleInputChange}
                                >
                                    <option value="1">1</option>
                                    <option value="2">2</option>
                                    <option value="3">3</option>
                                    <option value="4">4</option>
                                </SelectGrade>
                            </td>
                        </TableRow>
                        <TableRow>
                            <td>
                                <InputInform></InputInform>
                            </td>
                            <td>
                                <InputField
                                    type="email"
                                    name="email"
                                    value={formData.email}
                                    onChange={handleInputChange}
                                />
                            </td>
                        </TableRow>
                        <TableRow>
                            <td>
                                <InputInform></InputInform>
                            </td>
                            <td>
                                <InputField
                                    type="tel"
                                    name="phoneNumber"
                                    value={formData.phoneNumber}
                                    onChange={handleInputChange}
                                />
                            </td>
                        </TableRow>
                        <TableRow>
                            <td>
                                <InputInform></InputInform>
                            </td>
                            <td>
                                <InputField
                                    type="text"
                                    name="userID"
                                    value={formData.userID}
                                    onChange={handleInputChange}
                                />
                            </td>
                        </TableRow>
                        <TableRow>
                            <td>
                                <InputInform></InputInform>
                            </td>
                            <td>
                                <InputField
                                    type="password"
                                    name="password"
                                    value={formData.password}
                                    onChange={handlePasswordChange}
                                />
                            </td>
                        </TableRow>
                        <TableRow>
                            <td>
                                <InputInform> </InputInform>
                            </td>
                            <td>
                                <InputField
                                    type="password"
                                    name="confirmPassword"
                                    value={formData.confirmPassword}
                                    onChange={handleConfirmPasswordChange}
                                />
                                {/*    */}
                                <p style={{fontSize: '14px', color: 'red'}}>{passwordMatch}</p>
                            </td>
                        </TableRow>
                    </TBody>
                </FormTable>
                <Button type="submit"></Button>
            </SignupForm>
        </Wrapper>
    );
}

export default Signup;