import React, {useState, useEffect} from 'react';
import {useParams} from 'react-router-dom';
import styled from "styled-components";
import Header from "../components/Header";
import BookFrame from "../components/BookFrame";


const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 180px;
`;

const BookImage = styled.img`
  width: 100%;
  height: auto;
  border-radius: 5px;
  box-shadow: 0px 7px 4px rgba(0, 0, 0, 0.2);
`;

const BookTitle = styled.p`
  color: black;
  font-size: 15px;
  font-weight: 600;
  margin-top: 5px;
  margin-left: 2px;
`;
const ContentWrapper = styled.div`
  width: 1000px;
`;
const Title = styled.h2`
  margin: 50px 0;
`;
const Keyword = styled.span`
  color: #1d5d90;
`;
const BookList = styled.ul`
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  list-style: none;
  padding: 0;
`;
const BookItem = styled.li`
  margin-right: 20px; /*      */
`;

function SearchResults() {
    const {keyword} = useParams(); // URL   
    const [searchResults, setSearchResults] = useState([]);
    const [error, setError] = useState(null); //   

    useEffect(() => {
        //    API  (:  )
        fetch(`http://localhost:8080/book/search?keyword=${keyword}`)
            .then((response) => {
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                return response.json();
            })
            .then((data) => {
                setSearchResults(data);
                setError(null); //     
            })
            .catch((error) => {
                console.error('Error searching books: ', error);
                setError(error); //      
            });
    }, [keyword]); // keyword   useEffect 

    if (error) {
        return <div>Error: {error.message}</div>; //      
    }

    return (
        <Wrapper>
            <Header/>
            <ContentWrapper>
                <Title><Keyword>'{keyword}'</Keyword>    </Title>
                <BookList>
                    {searchResults.map((book) => (
                        <BookItem key={book.bookId}>
                            <BookFrame book={book}/>
                        </BookItem>
                    ))}
                </BookList>
            </ContentWrapper>
        </Wrapper>
    );
}

export default SearchResults;
