import React, { useState } from "react";
import styled from "styled-components";
import { createReservation } from "../api/FacilityReserveapi";
import {darken} from "polished";

const ModalOverlay = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
`;

const ModalContent = styled.div`
    width: 300px;
    background: white;
    border-radius: 5px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;    
    align-items: center;
`;
const ModalHeader = styled.div`
    background: #EFF2FF;
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    justify-content: space-between; /*     */
`;
const CloseButton = styled.button`
    border: none;
    cursor: pointer;
    margin-right: 5px;
`;
const ModalTitle = styled.p`
    font-size: 17px;
    padding-left: 15px;
    font-weight: 700;
`;
const ReservationBtn = styled.button`
  justify-content: center;
  background: #A5B3FF;
  color: #FFF;
  border: none;
  border-radius: 5px;
  width: 80%;
  height: 40px;
  margin-bottom: 10px;

  &:hover {
    background: ${(props) =>
            props.backgroundColor
                    ? darken(0.1, props.backgroundColor) //    
                    : "#123456"};
    /*       */
  }
`;
function Modal({ isOpen, onClose, onReservation, date, time, roomData}) {
  const rezDate = date;
  const rezTime = time;

  const [rezPeopleNum, setRezPeopleNum] = useState(3);

  const storedUserInfo = localStorage.getItem("userInfo");           //     
  const userInfo = storedUserInfo ? JSON.parse(storedUserInfo) : null;

  if (!isOpen) return null;

  const handleReservation = async () => {
          try {

              // createReservation      
              await createReservation(
              parseInt(rezPeopleNum, 10),
              rezDate,
              rezTime,
              roomData.roomId,
              parseInt(userInfo.userStuId, 10),
              userInfo.userName);

              alert(" .")
              onReservation();
              window.location.reload();
              console.log("  .");
          } catch (error) {
              console.error(" :", error);
              alert(error);
              window.location.reload();
          }
      };

  return (
    <ModalOverlay onClick={onClose}>
      <ModalContent onClick={(e) => e.stopPropagation()}>
        <ModalHeader>
            <ModalTitle> </ModalTitle>
            <CloseButton onClick={onClose}></CloseButton>
        </ModalHeader>
        <p>: {roomData.roomFloor} {roomData.roomName}</p>
        <p>: {rezDate}</p>
        <div style={{ textAlign: 'center' }}>
            <p>:</p>
            {rezTime.map((slot, index) => (
                <p key={index}>{slot}</p>
            ))}
        </div>
        <p> : {"\n"}
        <input
                type="text"
                value={rezPeopleNum}
                onChange={(e) => setRezPeopleNum(e.target.value)}

              /> </p>
        <p>  ?</p>
        <ReservationBtn onClick={handleReservation}></ReservationBtn>
      </ModalContent>
    </ModalOverlay>
  );
}

export default Modal;
