import React, { useState, useEffect } from 'react';
import styled from "styled-components";
import Header from "../components/Header";
import Button from "../common/Button";
import { Link, useNavigate } from "react-router-dom";
import { fetchWithToken } from "../api/api"; // api.js  fetchWithToken  .

const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 180px;
`;

const LoginArea = styled.div`
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const Title = styled.p`
  font-size: 30px;
  font-weight: bold;
  margin-top: 40px;
`;

const InputArea = styled.div`
  width: 300px;
  height: 40px;
  border: 2px solid #cecece;
  display: flex;
  align-items: center;
  justify-content: center;

  &:first-of-type {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }

  &:last-of-type {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top: none;
  }
`;

const StyledInput = styled.input`
  border: none;
  width: 280px;
  height: 30px;
`;

const CheckboxLabel = styled.label`
  display: flex;
  align-items: center;
  margin-top: 10px;
`;

const CheckboxInput = styled.input`
  margin-right: 5px;
`;

const LoginForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
`;

function Login({onLogin}) {
    //    
    const [formData, setFormData] = useState({
        userId: localStorage.getItem('savedId') || '', //      
        userPw: '',
    });

    //     
    const [saveId, setSaveId] = useState(false);

    //         
    const [loginStatus, setLoginStatus] = useState('');

    const navigate = useNavigate();
    //      
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value,
        });
    };
    //         
    useEffect(() => {
        const savedId = localStorage.getItem('savedId');
        if (savedId) {
            setFormData({
                ...formData,
                userId: savedId,
            });
            setSaveId(true);
        }
    }, []); //         

    //        
    const handleSaveIdChange = (e) => {
        setSaveId(e.target.checked);
    };

    //     
    const handleSubmit = async (e) => {
        e.preventDefault();
        //      
        try {
            //  API  URL
            const loginUrl = 'http://localhost:8080/api/authenticate';

            //     (:  )
            const loginData = {
                userId: formData.userId,
                userPw: formData.userPw,
            };

            // fetchWithToken    API 
            const response = await fetchWithToken(loginUrl, 'POST', loginData);
            const token = response.headers.get("authorization");
            const res = await response.json()

            //       .
            if (token) {
                //    
                setLoginStatus(' ');

                localStorage.setItem("userInfo", JSON.stringify(res)); //   
                localStorage.setItem('token', token); //    

                alert(res.userName + ' !');

                //     
                navigate('/');
            } else {
                //   
                setLoginStatus(' :  ');
            }
        } catch (error) {
            console.error(' :', error);
            setLoginStatus('   ');
        }

        //         .
        if (saveId) {
            //    
            localStorage.setItem('savedId', formData.userId);
        } else {
            //      
            localStorage.removeItem('savedId');
        }
    };

    return (
        <Wrapper>
            <Header />
            <LoginArea>
                <Title></Title>
                <LoginForm onSubmit={handleSubmit}>
                    <InputArea>
                        <StyledInput
                            type="text"
                            id="userId"
                            name="userId"
                            value={formData.userId}
                            onChange={handleInputChange}
                            required
                        />
                    </InputArea>
                    <InputArea>
                        <StyledInput
                            type="password"
                            id="userPw"
                            name="userPw"
                            value={formData.userPw}
                            onChange={handleInputChange}
                            required
                        />
                    </InputArea>
                    <CheckboxLabel>
                        <CheckboxInput
                            type="checkbox"
                            id="saveId"
                            name="saveId"
                            checked={saveId}
                            onChange={handleSaveIdChange}
                        />
                         
                    </CheckboxLabel>
                    <Button type="submit"></Button>
                </LoginForm>
                <p>
                    &nbsp;&nbsp;   <br /> 
                </p>
                <Button backgroundColor="#FDDC3F" textColor="#3A2929" type="submit">
                     
                </Button>
                <Link to="/signup" style={{ textDecoration: 'none' }}>
                    <Button border="1px solid #1D2B74" backgroundColor="white" textColor="black">
                        
                    </Button>
                </Link>
                <p>{loginStatus}</p>
            </LoginArea>
        </Wrapper>
    );
}

export default Login;
