import React, { useState } from "react";
import styled from "styled-components";
import ChatWindow from "./ChatWindow"; // ChatWindow  

const Wrapper = styled.div`
  width: 100%;
  height: 560px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  padding-top: 30px;
`;

const TitleArea = styled.div`
  padding-left: 20px;
  height: 40px;
  width: 900px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  justify-content: flex-start;
  margin-bottom: 10px;
`;

const StyledTitle = styled.p`
  color: #000;
  font-family: Inter;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;

  &:not(:last-child) {
    font-size: 30px;
  }
`;

const ChatFrame = styled.div`
    width: 900px;
    height: 480px;
    border: none;
    border-radius: 15px;
    background: #fff;
    box-shadow: 0px 4px 4px 3px rgba(0, 0, 0, 0.25);   
    display: flex; /*     */
    justify-content: flex-start; /*     */
    flex-direction: column;
    align-items: center;
`;
const ChatInput = styled.input`
    width: 800px;
    height: 25px;
    padding: 10px;
    margin-top: 10px;
    border-radius: 10px;
    background: #FFF;
    border: none;
    box-shadow: 0px 4px 8px 5px rgba(0, 0, 0, 0.15);
`;

function ChatArea() {
  const [messages, setMessages] = useState([]);

  const handleSendMessage = (message) => {
    setMessages([...messages, { text: message, isUser: true }]);
    // AI        
    // setMessages([...messages, { text: aiResponse, isUser: false }]);
  };

  return (
    <Wrapper>
      <TitleArea>
        <StyledTitle>ChatLPT</StyledTitle>
        <StyledTitle>    </StyledTitle>
      </TitleArea>
      <ChatFrame>
      <ChatWindow messages={messages} /> {/* ChatWindow   */}
      <ChatInput
        placeholder=" ..."
        onKeyPress={(e) => {
          if (e.key === "Enter") {
            handleSendMessage(e.target.value);
            e.target.value = "";
          }
        }}
      />
      </ChatFrame>
    </Wrapper>
  );
}

export default ChatArea;
