import React, { useState, useEffect } from "react";
import styled from "styled-components";
import BookFrame from "../components/BookFrame";

const Wrapper = styled.div`
  width: 100%;
  color: #000000;
`;

const SectionWrapper = styled.div`
  width: 100%;
  overflow-x: auto; /*    */
  white-space: nowrap; /*      */
`;

const Title = styled.p`
  margin-left: 20px;
  color: #000000;
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 10px;
`;

function BookList({ book }) {
    const [topBooks, setTopBooks] = useState([]);
    const [newBooks, setNewBooks] = useState([]);
    const [fictionBooks, setFictionBooks] = useState([]);

    useEffect(() => {
        //   API   
        const fetchData = async () => {
            try {
                const [topResponse, fictionResponse, newResponse] = await Promise.all([
                    fetch("http://localhost:8080/book/top"),
                    fetch("http://localhost:8080/book/fiction"),
                    fetch("http://localhost:8080/book/new"),
                ]);

                if (!topResponse.ok || !newResponse.ok || !fictionResponse.ok) {
                    throw new Error("Network response was not ok");
                }

                const topData = await topResponse.json();
                const fictionData = await fictionResponse.json();
                const newData = await newResponse.json();


                setTopBooks(topData);
                setFictionBooks(fictionData);
                setNewBooks(newData);
            } catch (error) {
                console.error("Error fetching data: ", error);
            }
        };

        //    
        fetchData();
    }, []);

    return (
        <Wrapper>
            {/* TOP  */}
            <Title>  TOP 10 !!</Title>
            <SectionWrapper>
                {topBooks.map((book, index) => (
                    <div style={{ display: 'inline-block', margin: '20px' }} key={index}>
                        <BookFrame book={book} />

                    </div>
                ))}
            </SectionWrapper>

            {/*   */}
            <Title>    </Title>
            <SectionWrapper>
            {fictionBooks.map((book, index) => (
            <div style={{ display: 'inline-block', margin: '20px' }} key={index}>
            <BookFrame book={book} />
            </div>
            ))}
            </SectionWrapper>

            {/*   */}
            <Title>   !</Title>
            <SectionWrapper>
                {newBooks.map((book, index) => (
                    <div style={{ display: 'inline-block', margin: '20px' }} key={index}>
                        <BookFrame book={book} />
                    </div>
                ))}
            </SectionWrapper>
        </Wrapper>
    );
}

export default BookList;
