//src/pages/FacilityReservation.js
import React, { useState, useEffect } from "react";
import styled from "styled-components";
import Header from "../components/Header";
import Room from "../components/Room";
import MyDatePicker from "../components/MyDatePicker";
import MyTimePicker from "../components/MyTimePicker";
import Button from "../common/Button";
import { searchFacility } from "../api/FacilityReserveapi"; // createReservation  

const Wrapper = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 180px;
`;

const Title = styled.p`
  text-align: left;
  color: #000;
  font-family: Inter;
  font-size: 25px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
`;

const RoomTypeArea = styled.div`
  justify-content: flex-end;
  align-items: center;
  display: flex;
  margin-bottom: 30px;
`;

const RoomTypeBtn = styled.button`
  width: 100px;
  height: 40px;
  border-radius: 30px;
  background: #eff2ff;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  border: none;
  color: #000;
  font-family: Inter;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;

  &:not(:last-child) {
    margin-right: 100px;
  }

  &:hover,
  &:focus {
    font-weight: 550;
    background: #a5b3ff;
    color: #fff;
  }
`;

const PickerArea = styled.div`
  width: 800px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 20px;
`;
const Result = styled.div`
  width: 880px;
`;
function groupByRoomFloor(searchResult) {
    const groupedResult = {};
    searchResult.forEach((roomData) => {
        const roomFloor = roomData.roomFloor;
        if (!groupedResult[roomFloor]) {
            groupedResult[roomFloor] = [];
        }
        groupedResult[roomFloor].push(roomData);
    });
    return groupedResult;
}
function FacilityReservation() {
    const [selectedMenu, setSelectedMenu] = useState("");
    const [selectedDate, setSelectedDate] = useState(new Date());
    const [selectedTimes, setSelectedTimes] = useState([]);
    const [searchResult, setSearchResult] = useState([]); //  

    const handleMenuClick = (menu) => {
        setSelectedMenu(menu);
    };

    const handleFacilitySearch = async () => {
        try {
            if (!selectedDate || selectedTimes.length === 0) {
                console.error("  .");

                return; //        
            }
            console.log(selectedMenu + " " + selectedDate + " " + selectedTimes)
            const result = await searchFacility(
                selectedMenu,
                selectedDate,
                selectedTimes,
            );
            setSearchResult(result);
        } catch (error) {
            console.error("  :", error);
            console.log(selectedMenu + " " + selectedDate + " " + selectedTimes);
            alert(error);
            setSelectedTimes([]);
        }
    };

    useEffect(() => {
        if (selectedTimes.length === 0) {
            setSearchResult([]);
        } else {
        handleFacilitySearch();
        }
    }, [selectedMenu, selectedDate, selectedTimes]);

    //   
    const groupedSearchResult = groupByRoomFloor(searchResult);

    return (
        <Wrapper>
            <Header />
            <Title></Title>
            <RoomTypeArea>
                <RoomTypeBtn
                    onClick={() => handleMenuClick("")}
                    style={selectedMenu === "" ? { background: "#a5b3ff", color: "#fff" } : {}}
                >
                    
                </RoomTypeBtn>
                <RoomTypeBtn
                    onClick={() => handleMenuClick("VR")}
                    style={selectedMenu === "VR" ? { background: "#a5b3ff", color: "#fff" } : {}}
                >
                    VR
                </RoomTypeBtn>
                <RoomTypeBtn
                    onClick={() => handleMenuClick("")}
                    style={selectedMenu === "" ? { background: "#a5b3ff", color: "#fff" } : {}}
                >
                    
                </RoomTypeBtn>
            </RoomTypeArea>
            <PickerArea>
                <MyDatePicker onDateChange={(date) => setSelectedDate(date)} />
                <MyTimePicker
                    selectedTimes={selectedTimes}
                    setSelectedTimes={setSelectedTimes}
                />
            </PickerArea>

            {Object.entries(groupedSearchResult).map(([floor, rooms]) => (
                <Result key={floor}>
                    <h2>{`${floor}`}</h2>
                    <div style={{display: 'flex', flexWrap: 'wrap'}}>
                        {rooms.map((roomData, index) => (
                            <Room
                                key={index}
                                roomData={{
                                    ...roomData,
                                    date: selectedDate.toISOString().split('T')[0],
                                    time: selectedTimes,
                                }}
                            />
                        ))}
                    </div>
                </Result>
            ))}
        </Wrapper>
    );
}

export default FacilityReservation;
