import React from "react";
import styled from "styled-components";
import { Link } from 'react-router-dom';

const BookWrapper = styled.div`
  position: relative;
  width: 180px; /*     */
  height: 270px;
  margin-right: -20px;
  transition: transform 0.3s ease; /*     */

  &:hover {
    transform: scale(1.05); /*     */
  }
`;

const BookImage = styled.img`
  width: 100%;
  height: 250px;
  border-radius: 5px;
  box-shadow: 0px 7px 4px rgba(0, 0, 0, 0.2);
`;

const BookTitle = styled.p`
  color: black;
  font-size: 15px;
  font-weight: 600;
  margin-top: 5px;
  margin-left: 2px;
  text-decoration: none;
`;
const ReturnDate = styled.p`
  color: red;
  font-size: 15px;
  font-weight: 600;
`;
const LoaningBookFrame = ({ book }) => {
    return (
        <Link to={`/book-detail/${book.bookId}`}> {/*     URL  */}
            <BookWrapper>
                <BookImage src={book.bookImage} alt={book.bookTitle} />
                <BookTitle>{book.bookTitle}</BookTitle>
                <ReturnDate>  : {book.returnDate}</ReturnDate>
            </BookWrapper>
        </Link>
    );
};



export default LoaningBookFrame;