import React, { useState, useEffect } from "react";
import styled from "styled-components";
import LoaningBookFrame from "./LoaningBookFrame";

const Wrapper = styled.div`
  margin-top: 20px;
  width: 1000px;
  color: #000000;
`;

const SectionWrapper = styled.div`
  width: 100%;
  overflow-x: auto; /*    */
  white-space: nowrap; /*      */
`;

const Title = styled.p`
  margin-left: 20px;
  color: #000000;
  font-size: 25px;
  font-weight: 700;
  margin-bottom: 10px;
`;

function LoaningBookList({ book }) {
    const [loanBooks, setLoanBooks] = useState([]);

    const storedUserInfo = localStorage.getItem("userInfo");
    const userInfo = storedUserInfo ? JSON.parse(storedUserInfo) : null;

    const userId = userInfo.userId;
    useEffect(() => {

        //   API   
        const fetchData = async () => {
            try {
                const loanResponse = await fetch(`http://localhost:8080/book/loaning?userId=${userId}`);

                if (!loanResponse.ok) {
                    throw new Error("Network response was not ok");
                }

                const loanData = await loanResponse.json();

                setLoanBooks(loanData);
            } catch (error) {
                console.error("Error fetching data: ", error);
            }
        };

        //    
        fetchData();
    }, [userId]);

    return (
        <Wrapper>
            <Title>  </Title>
            <SectionWrapper>
                {loanBooks.map((book, index) => (
                    <div style={{ display: 'inline-block', margin: '20px' }} key={index}>
                        <LoaningBookFrame book={book} />

                    </div>
                ))}
            </SectionWrapper>
        </Wrapper>
    );
}

export default LoaningBookList;
