import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import axios from "axios";
import HeartButton from "../common/HeartButton";
import ReserveButton from "../common/ReserveButton";
import DatePicker from "react-datepicker";
import moment from "moment";
import "react-datepicker/dist/react-datepicker.css";
import "../modalStyle.css";

const Wrapper = styled.div`
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  z-index: 1000;
`;

const BookTitle = styled.p`
  font-family: 'PilseungGothic';
  font-weight: normal;
  font-style: normal;
  text-align: left;
  font-size: 63px;
  line-height: normal;
  margin-top:0;
  margin-bottom: 0px;
  word-wrap: break-word;
`;

const BookAuthor = styled.p`
  text-align: left;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  color: rgba(255, 255, 255, 0.77)
`;
const BtnArea = styled.div`
  position: relative;;
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
`;

const LoanBtn = styled.button`
  width: 300px;
  height: 65px;
  background: #ffffff;
  color: #000000;
  border-radius: 5px;
  border: none;
  font-weight: 700;
  font-size: 17px;
  cursor: pointer;
`;

const FormTable = styled.table`
  width: 700px;
  border-collapse: collapse;
  color: rgba(255, 255, 255, 0.77);
  margin-top: 30px;
`;

const ColGroup = styled.colgroup`
  col:nth-child(1) {
    width: 20%;
  }
  col:nth-child(2) {
    width: 80%;
  }
`;

const TBody = styled.tbody``;

const TableRow = styled.tr`
  td {
    padding-bottom: 20px;
  }
`;

const Info = styled.p`
  font-size: 14px;
  margin: 0;
  font-weight: bold;
`;

const InfoContent = styled.p`
  font-size: 14px;
  margin: 0;

  font-weight: bold;
`;

const BookInfo = ({ bookInfo }) => {
  const [isLiked, setIsLiked] = useState(false);
  const [isBookLoaned, setIsBookLoaned] = useState(false);
  const [loanButtonText, setLoanButtonText] = useState("");
  const [selectedDate, setSelectedDate] = useState(null);
  const [isReserveOpen, setIsReserveOpen] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const { bookId } = useParams();

  const storedUserInfo = localStorage.getItem("userInfo");
  const userInfo = storedUserInfo ? JSON.parse(storedUserInfo) : null;

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const today = new Date();
  const monthFromToday = new Date(today);
  monthFromToday.setMonth(monthFromToday.getMonth() + 1);

  useEffect(() => {
      if (userInfo && bookId) {
        //     API 
        const userId = userInfo.userId;

        //  API     
        axios
          .get(`http://localhost:8080/book/checkLike?userId=${userId}&bookId=${bookId}`)
          .then((response) => {
            //   
            const likeStatus = response.data;
            // "on" 
            if (likeStatus === "on") {
              setIsLiked(true);
            }
          })
          .catch((error) => {
            console.error("Error checking like status: ", error);
          });

        axios
           .get(`http://localhost:8080/book/loan?userId=${userId}&bookId=${bookId}`)
           .then((response) => {
              const loanStatus = response.data;
              if (loanStatus === "able") {
                setLoanButtonText("");
                setIsBookLoaned(false);
              } else if(loanStatus === "unable") {
                setLoanButtonText(" ");
                setIsBookLoaned(true);
              }
            })
            .catch((error) => {
              console.error("Error checking book loan status: ", error);
            });

        axios
           .get(`http://localhost:8080/book/reserve?bookId=${bookId}`)
           .then((response) => {
           const reserveStatus = response.data;
           if (reserveStatus === " ") {
                setIsReserveOpen(false);
           } else {
                setIsReserveOpen(true);
           }
           })
           .catch((error) => {
           console.error("Error checking book loan status: ", error);
           });
        }
      }, [userInfo, bookId]);

  //     
  const handleLikeClick = () => {
    if(!userInfo) {
        alert(" ");
        return;
    }

    //   bookId .
    const bookId = bookInfo.bookId;
    const userId = userInfo.userId;

    // HTTP POST  .
    axios
      .post('http://localhost:8080/book/like', { bookId, userId }, {
      headers: {
         'Content-Type': 'application/json',
         },
      })
      .then((response) => {
        const likeStatus = response.data;
        //    
        if(likeStatus === 1) {
            setIsLiked(!isLiked); //    
            alert("  ");
        } else if(likeStatus === 0) {
            setIsLiked(!isLiked);
            alert("  ");
        } else {
            alert("");
        }
        console.log(response.data);
      })
      .catch((error) => {
        //    
        console.error(error);
        alert("ERROR");
      });
  };

  const handleLoanClick = () => {
      if (!userInfo) {
        alert(" ");
        return;
      }

      if (isBookLoaned) {
        alert("    ");
        return;
      }

      const userId = userInfo.userId;

      axios
        .post('http://localhost:8080/book/loan', { bookId, userId }, {
          headers: {
            'Content-Type': 'application/json',
          },
        })
        .then((response) => {
          const loanStatus = response.data;
          if (loanStatus === 0) {
            setIsBookLoaned(true);
            setLoanButtonText(" ");
            alert(" ");
          } else if(loanStatus === 1) {
            alert("   .");
          }
        })
        .catch((error) => {
          console.error(error);
          alert("ERROR");
        });
    };

    const handleReserveClick = () => {
        if(!userInfo) {
            alert(" ");
            return;
        }

        // 
        setIsReserveOpen(true);

    };

    const handleDateChange = (date) => {
        setSelectedDate(date);
    };

    const handleReserveConfirm = () => {
        if (selectedDate) {
           //    .
           const reserveDate = moment(selectedDate).format("YYYY-MM-DD");

            axios
                .post('http://localhost:8080/book/reserve', {
                    bookId: bookId,
                    bookRezDate: reserveDate,
                    userId: userInfo.userId
                })
                .then((response) => {
                    const reserveStatus = response.data;
                       if(reserveStatus === " ") {
                         alert(" ")
                         setIsReserveOpen(false);
                       } else if(reserveStatus === "") {
                         alert("   ")
                         setIsReserveOpen(false);
                       } else{
                          alert("    ")
                          setIsReserveOpen(false);
                       }
                    })
                .catch((error) => {
                 console.error(error);
                        alert("   .");
                      });
                  } else {
                    alert("  .");
                  }
                };

  return (
    <Wrapper>
      <BookTitle>{bookInfo.bookTitle}</BookTitle>
      <BookAuthor>{bookInfo.author}</BookAuthor>
      <BtnArea>
          <LoanBtn onClick={handleLoanClick}>
              {isBookLoaned ? loanButtonText === " " ? " "
               : "" : ""}
          </LoanBtn>

        {isReserveOpen && (
            <ReserveButton onClick={openModal} />
        )}
        <HeartButton isLiked={isLiked} onClick={handleLikeClick}/>
        {isModalOpen && (
            <div className="modal">
            <div className="modal-content">
            <h2 className="black-text">   </h2>
            <p className="black-text">*    .</p>
            <p className="black-text">*         .</p>
            <p className="black-text">*      .</p>
            <p className="black-text">*      .</p>
            <DatePicker
                selected={selectedDate}
                onChange={handleDateChange}
                dateFormat="yyyy-MM-dd"
                minDate={today} //   
                maxDate={monthFromToday} //      
                placeholderText="  "
              />
              <button onClick={handleReserveConfirm}></button>
              <button onClick={closeModal}></button>
            </div>
            </div>
           )}
      </BtnArea>
      <FormTable>
        <ColGroup>
          <col />
          <col />
        </ColGroup>
        <TBody>
          <TableRow>
            <td>
              <Info></Info>
            </td>
            <td>
              <InfoContent>{bookInfo.category}</InfoContent>
            </td>
          </TableRow>
          <TableRow>
            <td>
              <Info></Info>
            </td>
            <td>
              <InfoContent>{bookInfo.publishedDate}</InfoContent>
            </td>
          </TableRow>
          <TableRow>
            <td>
              <Info></Info>
            </td>
            <td>
              <InfoContent>{bookInfo.publisher}</InfoContent>
            </td>
          </TableRow>
          <TableRow>
            <td>
              <Info></Info>
            </td>
            <td>
              <InfoContent>{bookInfo.floor}</InfoContent>
            </td>
          </TableRow>
        </TBody>
      </FormTable>
    </Wrapper>
  );
};

export default BookInfo;